.gameover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, .2);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup {
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 250px;
    background-color: white;
    border-radius: 15%;
}
